    background-position: left 100% top 50%;
    background-size: 0.4em auto, 100%;
}

select:focus,
input:focus {
    outline: none;
}

input {
    font-size: 1.1em;
    background-color: transparent;
}

section {
    width: 95%;
    margin: auto auto;
    padding: 15vh 0;
    background-color: rgb(212, 198, 198);
}


/* ===== Other elements ===== */

a {
    color: inherit;
    text-decoration: none;
}

ul {
    list-style-type: none;
    padding: 0;
    margin: 0;
}

li:hover {
    color: #6a983c;
}

button {
    display: block;
    font-family: "Poppins", sans-serif;
    font-weight: bold;
    box-sizing: border-box;
    border: none;
    font-size: 1.3em;
    line-height: 1.2em;
    background-color: transparent;
    padding: 2vh 2vw;
    border-radius: 12px;
}

button:hover {
